import { Component, OnInit } from '@angular/core';
import { ProductRegistrationService } from '../product-registration.service';
import { Observable } from 'rxjs';
import { Product } from '../product';

@Component({
  selector: 'app-productdetails',
  templateUrl: './productdetails.component.html',
  styleUrls: ['./productdetails.component.css']
})
export class ProductdetailsComponent implements OnInit {

  constructor(private service:ProductRegistrationService) { }

 
  product:Product;
  flag=0;
  flag2=0;
  name;
  message2;
  message:Product;
  ngOnInit() {
  }
  findbyna()
  {
    this.flag=1;
    var a=prompt("Write the name for its details");
    this.findbyuser(a);
  }
  delbyname()
  {
    var a=prompt("Write the name for deleting its details");
    this.deletebyuser(a);
  }
  showUsers()
  {
    this.flag=1;
    this.product=this.service.productData();
  }
  findbyuser(a){
    this.flag=1;
    let response = this.service.findbyName(a);
    response.subscribe((data) => this.message = (data));
  }
  deletebyuser(a){
    var e=confirm("Do you really want to delete the record?")
    if(e==true){
    let response = this.service.deletebyName(a);
    response.subscribe((data) => this.message2 = (data));
    this.flag2=1;
    }
  }
  deleteUsers() {
    var e=confirm("Do you really want to delete all the records?")
    if(e==true){
    let response = this.service.deleteAllProducts();
    response.subscribe((data) => this.name = (data));
    
    }
      // .subscribe(
      //   (data) => {
      //     console.log(data);
      //     this.showUsers();
      //   },
      //   error => console.log('ERROR: ' + error));
  }
  // private searchUsers() {
  //   this.service.getUsersByName(this.name)
  //     .subscribe(user1 => this.user1 = user1);
  // }
  myimage1:string="assets/img/PS.jpg";
  myimage2:string="assets/img/details.jpg";

}
