import { Component, OnInit } from '@angular/core';
import { UserRegistrationService } from '../user-registration.service';
import { User } from '../user';
import { Observable } from 'rxjs';
import { Router } from '@angular/router';
@Component({
  selector: 'app-userdata',
  templateUrl: './userdata.component.html',
  styleUrls: ['./userdata.component.css']
})
export class UserdataComponent implements OnInit {

  constructor(private service:  UserRegistrationService,private router: Router) { }
  user:User;
  flag=0;
  flag2=0;
  name;
  message2;
  message:User;
  ngOnInit() {
  }
  taketoSign(){
    this.router.navigateByUrl('sign');
  }
  findbyna()
  {
    this.flag=1;
    var a=prompt("Write the name for its details");
    this.findbyuser(a);
  }
  delbyname()
  {
    var a=prompt("Write the name for deleting its details");
    this.deletebyuser(a);
  }
  showUsers()
  {
    this.flag=1;
    this.user=this.service.userData();
  }
  findbyuser(a){
    this.flag=1;
    let response = this.service.findbyName(a);
    response.subscribe((data) => this.message = (data));
  }
  deletebyuser(a){
    var e=confirm("Do you really want to delete the record?")
    if(e==true){
    let response = this.service.deletebyName(a);
    response.subscribe((data) => this.message2 = (data));
    this.flag2=1;
    }
  }
  deleteUsers() {
    var e=confirm("Do you really want to delete all the records?")
    if(e==true){
    let response = this.service.deleteAll();
    response.subscribe((data) => this.name = (data));
    
    }
      // .subscribe(
      //   (data) => {
      //     console.log(data);
      //     this.showUsers();
      //   },
      //   error => console.log('ERROR: ' + error));
  }
  // private searchUsers() {
  //   this.service.getUsersByName(this.name)
  //     .subscribe(user1 => this.user1 = user1);
  // }
  myimage1:string="assets/img/PS.jpg";
  myimage2:string="assets/img/details.jpg";
}
